import { useEffect, useRef, useState } from "react";
import styled from "styled-components";

const DropDownWrapper = styled.div`
  position: relative;
  & * {
    user-select: none;
  }
`;

const DropDownHeader = styled.div`
  /* padding : 0.938rem 3.438rem; */
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.625rem;

  border-radius: 3.938rem;
  padding: ${({ paddingY = "17" }) => paddingY}px
    ${({ paddingX = "55" }) => paddingX}px;

  background: ${({
    bg_color = "linear-gradient(217.29deg, #0E7ED8 10.81%, #04A2E4 92.27%);",
  }) => bg_color};
  ${({ border }) =>
    border && "border: 1.05284px solid rgba(255, 255, 255, 0.2);"}

  text-align: center;
  font-weight: 400;
  font-size: 1.125rem;
  color: ${({ color = "white" }) => color};

  &:hover {
    cursor: pointer;
  }
`;

const DropDownListContainer = styled.ul`
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 0.625rem;

  top: 3.75rem;
  left: ${({ type }) => (type === "products_mobile" ? -200 : 0)}%;
  width: 100%;
  min-width: 12.5rem;
  padding: 0.938rem 0.75rem;
  background-color: white;
  border-radius: 0.75rem;
  background: #e7f5ff;
  box-sizing: border-box;

  text-align: center;

  &,
  & li {
    z-index: 999;
  }

  &:first-child {
    padding-top: 0.8em;
  }
`;

const ListItem = styled.li`
  list-style: none;
  width: 100%;
  cursor: pointer;
  color: #1073d3;

  & a {
    color: inherit;
  }

  &:hover,
  & a:hover {
    color: #0a4a8a;
  }
`;

const Languages = ["Ru", "Eng"];

const ProductTitles = [
  { href: "https://telejet.socialjet.pro/signin", name: "Telegram" },
  { href: "https://rocketvideo.socialjet.pro/login/", name: "Rocket Video" },
  { href: "https://socialjet.pro/publishers.html", name: "Я - паблишер" },
];

function useOutsideAlerter(ref, setIsOpen) {
  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setIsOpen(false);
      }
    }
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [ref]);
}

const Select = ({
  type,
  name,
  bg_color,
  paddingX,
  paddingY,
  color = "white",
  onChange,
  border = false,
  chevron = true,
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedOption, setSelectedOption] = useState(null);

  const wrapperRef = useRef(null);
  useOutsideAlerter(wrapperRef, setIsOpen);

  const toggling = () => setIsOpen(!isOpen);

  const onOptionClicked = (value) => () => {
    setSelectedOption(value);
    setIsOpen(false);

    onChange(value);
  };

  return (
    <DropDownWrapper ref={wrapperRef}>
      <DropDownHeader
        border={border}
        onClick={toggling}
        bg_color={bg_color}
        paddingY={paddingY}
        paddingX={paddingX}
        color={color}
      >
        {selectedOption ? selectedOption : name}
        {chevron && (
          <svg
            width="12"
            height="7"
            viewBox="0 0 12 7"
            fill={color}
            xmlns="http://www.w3.org/2000/svg"
            style={{ paddingTop: 3 + "px" }}
          >
            <path
              d="M5.46967 6.53033C5.76256 6.82322 6.23744 6.82322 6.53033 6.53033L11.3033 1.75736C11.5962 1.46447 11.5962 0.989593 11.3033 0.696699C11.0104 0.403806 10.5355 0.403806 10.2426 0.696699L6 4.93934L1.75736 0.696699C1.46447 0.403806 0.989593 0.403806 0.696699 0.696699C0.403806 0.989592 0.403806 1.46447 0.696699 1.75736L5.46967 6.53033ZM5.25 5L5.25 6L6.75 6L6.75 5L5.25 5Z"
              fill="%23040F6B"
            />
          </svg>
        )}
      </DropDownHeader>

      {isOpen && (
        <DropDownListContainer paddingX={paddingX} type={type}>
          {type === "languages" &&
            Languages.map((language, index) => (
              <ListItem key={index} onClick={onOptionClicked(language)}>
                {language}
              </ListItem>
            ))}
          {(type === "products" || type === "products_mobile") &&
            ProductTitles.map((title, index) => (
              <ListItem key={index}>
                <a
                  aria-label={"Перейти на продукт " + title.name}
                  href={title.href}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {title.name}
                </a>
              </ListItem>
            ))}
        </DropDownListContainer>
      )}
    </DropDownWrapper>
  );
};

export default Select;
